import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { LoginService } from './login.service';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { environment } from './../../../../environments/environment';
import { JwtService } from './../../../shared-ui/jwt.service';
import { AlertService } from './../../../shared-ui/alert/alert.service';
import { currentUser } from './../../../layouts/home-layout/user.model';

@Component({
  selector: 'app-login',
  templateUrl: 'login.component.html'
})
export class LoginComponent implements OnInit {
  currentUser: currentUser = new currentUser;
  loginInfo: any = {
    email: '',
    password: ''
  };
  userDetails: FormGroup;
  
  constructor(
    private loginService: LoginService,
    private toastr: ToastrService,
    private router: Router,
    private jwtService: JwtService,
    private alertService: AlertService
  ) { }

  ngOnInit() {
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
    this.router.navigate(['/company/list']);
  }

  doSignin(){
    this.alertService.clear();
    this.loginService.doSignin(this.loginInfo).subscribe((data) => {
      if(Object.keys(data).length){
        let userDetails = {
          id: data.id,
          firstName: data.firstName,
          lastName: data.lastName,
          fullName: data.fullName,
          emailAddress: data.emailAddress,
          customerAdmin: data.customerAdmin,
          successHackerAdmin: data.successHackerAdmin,
        }
        this.jwtService.saveCurrentUser(JSON.stringify(userDetails));
        this.router.navigate(['/company/list']);
      }else{
        this.alertService.error('Invalid email or password!');
      }
    }, (error) => {
      this.toastr.error('There are some server error! Please check connection.', 'Error');
    });
  }

 }
